<template>
	<view class="wrap">
		<view class="key-input">
			<view class="title">输入验证码</view>
			<view class="tips">验证码已发送至 {{mobile}}</view>
			<u-message-input :focus="true" :value="value" @change="change" @finish="finish" mode="bottomLine" :maxlength="maxlength"></u-message-input>
			<text :class="{ error: error }">验证码错误，请重新输入</text>
			<view class="captcha">
				<text :class="{ noCaptcha: show }" @tap="noCaptcha">收不到验证码点这里</text>
				<text :class="{ regain: !show }">{{ num }}秒后重新获取验证码</text>
			</view>
		</view>
		<u-mask :show="loading" :duration="400" :zoom="true" :custom-style="{background: 'rgba(255, 255, 255, 0.9)'}"></u-mask>
	</view>
</template>

<script>
import {
		mapState,
		mapMutations
	} from 'vuex';
export default {
	data() {
		return {
			loading:false,
			num:60,
			mobile:'',
			maxlength: 6,
			value: '',
			second: 60,
			show: false,
			error: false
		};
	},
	computed: {},
	onLoad(obj) {
		//console.log('mobile',obj)
		this.mobile= obj && obj.mobile
		this.getCaptcha()
	},
	methods: {
		// 收不到验证码选择时的选择
		noCaptcha() {
			let _this = this
			uni.showActionSheet({
				itemList: ['重新获取验证码'],
				success: function(res) {
					_this.getCaptcha()
				},
				fail: function(res) {
					
				}
			});
		},
		getCaptcha(){
				this.loading = true
				this.request({
					url: "auth/sendVericode",
					data: {
						mobile: this.mobile
					},
				}).then(data => {
					console.log(data);
					if (data && data.resphead && data.resphead.succeed) {
						this.show = false;
						this.loading = false
						this.num = 60;
						let loop = setInterval(() => {
							this.num--;
							if (this.num == 0) {
								this.show = true;
								clearInterval(loop);
							}
						}, 1000);
						uni.showToast({
							title: "验证码发送成功",
							icon: "none"
						});
					} else {
						this.loading = false
						uni.showToast({
							title: data.resphead.message || "验证码发送失败",
							icon: "none"
						});
					}
				});
			
		},
		// change事件侦听
		change(value) {
			// console.log('change', value);
		},
		// 输入完验证码最后一位执行
		finish(value) {
			// console.log('finish', value);
			this.request({
				url: "auth/loginByMobile",
				data: {
					mobile:this.mobile,
					vericode:value
				},
			}).then(data => {
				if (data && data.resphead && data.resphead.succeed) {
					let xuid=data.respbody.xuid
					this.login(data.respbody);
					this.request({
						url: "xuid/queryXuidInfo",
						data:{
							xuid:xuid
						},
					}).then(data => {
						if (data && data.resphead && data.resphead.succeed) {
							this.info(data.respbody);
							if(data.respbody.isxyzqemp && data.respbody.isxyzqemp =="1"){
								
								uni.switchTab({
									url: "/pages/home/index"
								})
							}else{
								uni.navigateTo({
									url: "/pages/home/tourist-home"
								})
							}
						} else {
							uni.showToast({
								title: data.resphead.message || "获取用户信息失败，请联系管理人员",
								icon: "none"
							});
						}
					});
					
				} else {
					uni.showToast({
						title: data.resphead.message || "登录失败",
						icon: "none"
					});
				}
			});
		},
		 ...mapMutations(['login','info'])
	}
};
</script>

<style lang="scss" scoped>
.wrap {
	padding: 80rpx 40rpx;
	height: 100%;
}

.box {
	margin: 30rpx 0;
	font-size: 30rpx;
	color: 555;
}

.key-input {
	padding: 30rpx 0;
	text {
		display: none;
	}
	.error {
		display: block;
		color: red;
		font-size: 30rpx;
		margin: 20rpx 0;
	}
}

.title {
	font-size: 50rpx;
	color: #333;
}

.key-input .tips {
	font-size: 30rpx;
	color: #333;
	margin-top: 20rpx;
	margin-bottom: 60rpx;
}
.captcha {
	//color: $u-type-warning;
	font-size: 30rpx;
	margin-top: 40rpx;
	.noCaptcha {
		display: block;
	}
	.regain {
		display: block;
	}
}
</style>
